import React from "react";
import {
  CardHeader,
  CardActionArea,
  Typography,
  CardMedia,
  CardContent,
  Card,
} from "@mui/material";
import Register from "../RegisterForm";
import LoginForm from "../LoginForm";
import ForgetPassword from "../ForgetForm";
import ResetPassword from "../ResetForm";
import ForgotUsername from "../forgotUsername";

//style here
import "./style.scss";
export default function ActionAreaCard({ title, subHeading, type }) {
  return (
    <Card sx={{ maxWidth: 1340 }} className="text-center Card my-5">
      <CardActionArea>
        <CardHeader title={title} className="Heading" />
        <CardContent>
          <Typography
            sx={{ fontSize: 14 }}
            color="text.secondary"
            className="para"
            gutterBottom
          >
            {subHeading ? subHeading : ""}
          </Typography>
          {type === "login" ? (
            <LoginForm />
          ) : type === "forgotPassword" ? (
            <ForgetPassword />
          ) : type === "resetPassword" ? (
            <ResetPassword />
          ) : type === "forgotUsername" ? (
            <ForgotUsername />
          ) : (
            <Register />
          )}
        </CardContent>
      </CardActionArea>
    </Card>
  );
}
