import React,{useState} from "react";
import styles from "./style.module.scss";
import { useFormik } from "formik";
import { useSelector, useDispatch } from "react-redux";
import { contactRequest } from "../../Redux/Contact/actions";
import * as Yup from "yup";

export default function Contact() {
  const dispatch = useDispatch();
  const [btnState, setBtnState] = useState(false);
  const contactUserSchema = Yup.object().shape({
    subject: Yup.string().trim()
      .max(16, "fullName should not exceed 16 characters")
      .required("please enter title"),
    message: Yup.string().trim()
      .max(550, "fullName should not exceed 550 characters")
      .required("description is required"),
    email: Yup.string().trim()
      .email("Please enter valid email address")
      .required("Please enter email address"),
      category: Yup.string().trim().required("category is required"),
  });
  const formik = useFormik({
    initialValues: {
      email: "",
      message: "",
      subject: "",
      category: "",
    },
    validationSchema: contactUserSchema,
    onSubmit: (values) => {
      setBtnState(true);
      dispatch(
        contactRequest({
          email: values.email,
          message: values.message,
          subject: values.subject,
          category: values.category,
          setBtnState:setBtnState,
          resetForm: formik.resetForm,
        })
      );
    },
  });
  return (
    <div className={`col-lg-12 ${styles.mainForm}`}>
      <div className="row">
        <form onSubmit={formik.handleSubmit} className={styles.formContact}>
          <div className="col-lg-7">
            <label className="text-start">Email</label>
            <input
              className={styles.input}
              name="email"
              value={formik.values.email}
              onChange={formik.handleChange}
              helperText={formik.touched.email && formik.errors.email}
            />
            {formik.touched.email && formik.errors.email ? (
              <p className={styles.error}>{formik.errors.email}</p>
            ) : (
              ""
            )}
          </div>
          <div className="col-lg-7">
            <label className="text-start">Category</label>
            <select
              name="category"
              value={formik.values.category}
              onChange={formik.handleChange}
              style={{ display: "block" }}
            >
              <option value="" disabled selected hidden>
                Select a color{" "}
              </option>
              <option value="red" label="Red">
                {" "}
                Red
              </option>
              <option value="blue" label="Blue">
                Blue
              </option>
              <option value="green" label="Green">
                Green
              </option>
            </select>
          </div>
          <div className="col-lg-7">
            <label className="text-start">Subject</label>
            <input
              className={styles.input}
              name="subject"
              value={formik.values.subject}
              onChange={formik.handleChange}
              helperText={formik.touched.subject && formik.errors.subject}
            />
            {formik.touched.subject && formik.errors.subject ? (
              <p className={styles.error}>{formik.errors.subject}</p>
            ) : (
              ""
            )}
          </div>
          <div className="col-lg-7">
            <label className="text-start">Description</label>
            <textarea
              className={styles.input}
              name="message"
              value={formik.values.message}
              onChange={formik.handleChange}
              helperText={formik.touched.message && formik.errors.message}
            />
            {formik.touched.message && formik.errors.message ? (
              <p className={styles.error}>{formik.errors.message}</p>
            ) : (
              ""
            )}
          </div>
          <div className="col-lg-3">
            <button type="submit" disabled={btnState}>Submit</button>
          </div>
        </form>
      </div>
    </div>
  );
}
