import {
  LOGIN,
  SIGN_UP,
  FORGOT_PASSWORD,
  RESET_PASSWORD,
  FORGOT_USERNAME,
  USER_LIST,
  GOOGLE_LOGIN,
  FACEBOOK_LOGIN,
} from "./const";
import { BASE_URL_API } from "../../config/const.js";
import axios from "axios";
import { toast } from "react-toastify";

export const SignUpRequest = (payload) => async (dispatch) => {
  const { userName, password, email, fullName } = payload;
  let url = `${BASE_URL_API}auth/register`;
  let data = {
    userName: userName,
    password: password,
    email: email,
    fullName: fullName,
  };
  try {
    let response = await axios.post(url, data);
    dispatch({
      type: SIGN_UP,
      payload: response.data.user,
    });
    toast.success(response.data.message);
    setTimeout(() => {
      window.location.href = "/login";
    }, 1500);
    payload.setBtnState(false);
  } catch (err) {
    toast.error(err.response.data.message);
    payload.setBtnState(false);
  }
};

export const Login = (payload) => async (dispatch) => {
  const { email, password } = payload;
  let url = `${BASE_URL_API}auth/login`;
  let data = {
    email: email,
    password: password,
  };
  let axiosConfig = {
    headers: {
      "Content-Type": "application/json",
    },
  };
  try {
    let response = await axios.post(url, data, axiosConfig);
    let Token = response.data.accessToken;
    dispatch({
      type: LOGIN,
      payload: response.data.user,
    });
    localStorage.setItem("Token", Token);
    if (response.data.user.admin) {
      localStorage.setItem("Admin", "True");
    }
    toast.success(response.data.message);
    payload.setBtnState(false);
    setTimeout(()=>{
    window.location.href="/";
    }, 1500)
  } catch (err) {
    toast.error(err.response.data.message);
    payload.setBtnState(false);
  }
};

export const isLogin = () => {
  let token = localStorage.getItem("Token");
  if (token) {
    return true;
  }
  return false;
};

export const logout = () => {
  localStorage.clear();
  window.location.href = "/login";
  // window.location.reload();
};

