import React from "react";
import styles from "./style.module.scss";
import Form from "./contactForm";

export default function Contact() {
  return (
    <div className={styles.mainDiv}>
      {/* <div className="container"> */}
      {/* <div className="container"> */}
      <div className="container">
        <div className="row">
          <div className={`col-lg-12 ${styles.heading}`}>
            <h1>Got a Question or Comment?</h1>
            <p>
              Most common questions about how best to use the features of the
              site,
            </p>
            <p>
              what your statistics mean, etc, are answered in our Instructional
              Videos and the FAQ page
            </p>
            <p>
              The easiest way to contact us is via the form below. You can
              usually expect a reply within one business day or faster..
            </p>
          </div>
            <Form />
        </div>
      </div>
    {/* </div> */}
    {/* </div> */}
    </div>
  );
}