import { CONTACT_FORM,PLANS_LIST } from "./const";
import { BASE_URL_API } from "../../config/const.js";
import axios from "axios";
import { toast } from "react-toastify";

export const contactRequest = (payload) => async (dispatch) => {
  const { message, category, email, subject, resetForm } = payload;
  let url = `${BASE_URL_API}user/contact-form`;
  let data = {
    message: message,
    category: category,
    email: email,
    subject: subject,
  };
  try {
    let response = await axios.post(url, data);
    dispatch({
      type: CONTACT_FORM,
      payload: response.data,
    });
    toast.success(response.data.message);
    payload.setBtnState(false);
    resetForm();
  } catch (err) {
    toast.error(err.response.data.message);
    payload.setBtnState(false);
  }
};

export const getPlans =()=> async(dispatch)=>{
    let url = `${BASE_URL_API}user/plansGet`;
    try{
        let response = await axios.get(url);
        dispatch({
          type: PLANS_LIST,
          payload: response.data,
        });
    }catch(err){
        toast.error(err.response.data.message)
    }

}
