import {PLAN_CHARGES} from "../stripe/const"
import { BASE_URL_API } from "../../config/const.js";
import axios from "axios";
import { toast } from "react-toastify";



export const stripePayement = (payload) => async (dispatch) => {
    const { token,priceId, email,cardElement,id, userId} = payload;
    let url = `${BASE_URL_API}user/charges`;
    let data = {
     token:token,
     priceId:priceId,
     email:email,
     id:id,
     userId:userId,
    }
    try {
      let response = await axios.post(url, data);
      dispatch({
        type: PLAN_CHARGES,
        payload: response.data,
      });
      toast.success(response.data.message);
      payload.setOpen(false);
      payload.setBtnState(false);
      cardElement.clear();
    } catch (err) {
      payload.setBtnState(false);
      cardElement.clear();
      toast.error(err.response.data.message);
    }
  };

  export const upgradeStripe = (payload) => async (dispatch) => {
    const {cardElement,id} = payload;
    let url = `${BASE_URL_API}user/upgradeStripe`;
    let data = {
     id:id,
    }
    try {
      let response = await axios.post(url, data);
      toast.success(response.data.message);
      payload.setOpen(false);
      payload.setBtnState(false);
      cardElement.clear();
    } catch (err) {
      payload.setBtnState(false);
      cardElement.clear();
      toast.error(err.response.data.message);
    }
  };