import {LOGIN,SIGN_UP, USER_LIST,GOOGLE_LOGIN,FACEBOOK_LOGIN} from "./const"
const initialState = {
    login: "",
    signUp:"",
    userList:[],
  };
  
  export default (state = initialState, action) => {
    switch (action.type) {
      case LOGIN:
        return {
          login: action.payload,
        };
        case GOOGLE_LOGIN:
          return {
            login: action.payload,
          };
          case FACEBOOK_LOGIN:
          return {
            login: action.payload,
          };
        case SIGN_UP:
        return {
          signUp: action.payload,
        };
        case USER_LIST:
          return {
            userList: action.payload,
          };
      default:
        return state;
    }
  };