import React, { useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { getPlans } from "../../Redux/Contact/actions";
import Modal from "@mui/material/Modal";
import Box from "@mui/material/Box";
import {
  Elements,
  CardElement,
  useElements,
  useStripe,
} from "@stripe/react-stripe-js";
import { stripePayement, upgradeStripe } from "../../Redux/stripe/actions";
import { isLogin } from "../../Redux/Auth/actions";
import styles from "./style.module.scss";
import { toast } from "react-toastify";

const Pricing = () => {
  const stripe = useStripe();
  const elements = useElements();
  const dispatch = useDispatch();
  const [open, setOpen] = useState(false);
  const [stripeId, setStripeId] = useState("");
  const [id, setid] = useState("");
  const [btnState, setBtnState] = useState(false);
  const handleOpen = (stripePlanId, id) => {
    setStripeId(stripePlanId);
    setid(id);
    setOpen(true);
  };
  const handleFree = (stripePlanId, id) => {
    setStripeId(stripePlanId);
    setid(id);
    handleSubmit();
  };
  const handleClose = () => setOpen(false);
  const planData = useSelector(
    (state) => state.contactReducer?.plansList?.response
  );
  const userData = useSelector((state) => state.authReducer?.login);
  const stripeData = useSelector((state) => state.stripeChages.chargeList);

  console.log(stripeData.stripeId, "herqe is stripeData")
  // console.log(userData, "here is userData")

  useEffect(() => {
    dispatch(getPlans());
  }, []);

  const handleSubmit = (stripe, elements) => async () => {
    const cardElement = elements.getElement(CardElement);
    setBtnState(true);
    await stripe.createToken(cardElement).then(async (res) => {
      if (res.error) {
        toast.error(res.error.message);
      } else {
        let token = res.token.id;
        dispatch(
          stripePayement({
            token: token,
            email: userData.email,
            priceId: stripeId,
            id: id,
            userId: userData.id,
            setBtnState: setBtnState,
            setOpen: setOpen,
            cardElement: cardElement,
          })
        );
      }
    });
  };
  return (
    <div className={`container ${styles.Pricing}`}>
      <div className="row justify-content-around">
        <div className={`col-lg-12 text-center py-4 ${styles.Heading}`}>
          <h1>Membership Options</h1>
        </div>
        {planData?.map((item) => {
          return (
            <div className={`text-center col-lg-4 ${styles.cardPrice}`}>
              {item.interval === "6 Months" ? (
                <button type="submit" className={styles.mostpopluar}>
                  Most Popular
                </button>
              ) :(
                <div className={styles.mostpopluarDiv}></div>
              )}
              <div className={styles.cardplan}>
                <h1>
                  {item.interval}
                </h1>
                <span className="mt-3">
                  {item.perMonth} $ {item.interval === "Free" ? "" : <p> /month{item.interval === "6 Months" ? "*" : item.interval === "Monthly" ? "":"*"}</p>}
                </span>
                <div>
              {item.trialDays} Days Free Trial 
              </div>
              {userData?.stripeId == item.id ? (
                <button type="submit">Paid</button>
              ) : stripeData?.stripeId == item.id ? (
                <button type="submit">Paid</button>
              ) : (
                <button
                  type="submit"
                  onClick={() => {
                    if(item.interval === "free"){
                      const cardElement = elements.getElement(CardElement);
                      setBtnState(true);
                          dispatch(
                            stripePayement({
                              email: userData.email,
                              priceId: item.stripePlanId,
                              id: item.id,
                              userId: userData.id,
                              setBtnState: setBtnState,
                              setOpen: setOpen,
                              cardElement: cardElement,
                            })
                          );
                    }else{
                      handleOpen(item.stripePlanId, item.id);
                    }
                  }}
                >
                  {!isLogin() || stripeData?.stripeId === undefined
                    ?  "Choose Plan"
                    : "Upgrade Plan"}
                </button>
              )}
              <div className={styles.billText}>
              {item.interval === "Monthly" ? "" :`*billed as one payment for a total of ${item.price} $`} 
              </div>
            </div>
            </div>
          );
        })}
      </div>
      <Modal keepMounted open={open} onClose={handleClose}>
        <Box className={styles.stripeCard}>
          <div>
            <CardElement
              className={styles.card}
              options={{
                style: {
                  base: {
                    color: "grey",
                  },
                },
              }}
            />
          </div>
          <button type="submit" onClick={handleClose}>
            Cancel
          </button>
          <button
            type="submit"
            className="float-end"
            onClick={handleSubmit(stripe, elements)}
            disabled={btnState}
          >
            Pay
          </button>
        </Box>
      </Modal>
    </div>
  );
};
export default Pricing;
