import {CONTACT_FORM,PLANS_LIST} from "./const"
const initialState = {
    contact: "",
    plansList:"",
  };
  
  export default (state = initialState, action) => {

    switch (action.type) {
      case CONTACT_FORM:
        return {
            contact: action.payload,
        };
        case PLANS_LIST:
        return {
            plansList: action.payload,
        };
      default:
        return state;
    }
  };