import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { isLogin, logout } from "../../Redux/Auth/actions";
import Logo from "../../Assets/logo.png";
//style import
import styles from "./style.module.scss";

export default function Header() {
  const [state, setState] = useState(false);
  const [route, setRoute] = useState("");
  const dispatch = useDispatch();

  useEffect(() => {
    let link = window.location.pathname.split("/");
    let point = link[1];
    setRoute(point);
  }, [window.location.pathname]);

  const sidemenu = () => {
    if (state) {
      setState(false);
    }
    if (!state) {
      setState(true);
    }
  };
  const handleLogout = () => {
    dispatch(logout());
  };
  let Admin = localStorage.getItem("Admin");

  return (
    <div className={`${styles.HeaderMain}`}>
      <div className={`row mx-0 ${styles.Header}`}>
        <div className={`col-lg-2 text-start col-sm-6 ${styles.logo}`}>
          <a href="/">
            <img alt="" src={Logo} />
          </a>
        </div>
        <div className="col-lg-10">
          <nav className="navbar navbar-expand-lg navbar-light navBar justify-content-end">
            <button
            type="button"
            className={`d-none ${styles.navButton}`}
            data-toggle="collapse"
            data-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
            onClick={sidemenu}
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          {state ? (
            <div className={`${styles.headerListResponsive}`}>
              <div className={`col-lg-12 text-center ${styles.listResponsive}`}>
              <ul className="justify-content-between">
                {
                  Admin &&(
                    <a href="/userList">
                    <li>Users List</li>
                    </a>
                  )
                }
                <a href="/pricing">
                <li>Pricing</li>
                </a>
                <a href="contact">
                <li>Contact</li>
                </a>
              </ul>
              </div>
            </div>
          ) : (
            ""
          )}
            <div
              className="collapse navbar-collapse"
              id="navbarSupportedContent"
            >
              <div className={`${styles.list}`}>
                <ul>
                  <a href="/">
                    <li>Home</li>
                  </a>
                  {Admin && (
                    <a href="/userList">
                      <li>Users List</li>
                    </a>
                  )}

                  <a href="/pricing">
                    <li>Pricing</li>
                  </a>
                  <a href="contact">
                    <li>Contact</li>
                  </a>
                </ul>
              </div>
              <div className={`${styles.list}`}>
                <ul>
                  {isLogin() ? (
                    <li>
                      <button className={`mx-4 ${styles.traingbuton}`}>
                        Start Training
                      </button>
                      <button onClick={handleLogout}>Log Out</button>
                    </li>
                  ) : route === "signup" ? (
                    <a href="/login">
                      <li>
                        <button>Log In</button>
                      </li>
                    </a>
                  ) : route === "login" ? (
                    <>
                      <a href="/signup">
                        <li>
                          <button className={`mx-4 ${styles.traingbuton}`}>
                            Start Training
                          </button>
                        </li>
                      </a>
                    </>
                  ) : (
                    <>
                     
                      <a href="/signup">
                        <li>
                          <button className={`mx-4 ${styles.traingbuton}`}>
                            Start Training
                          </button>
                        </li>
                      </a>
                      <a href="/login">
                        <li>
                          <button>Log in</button>
                        </li>
                      </a>
                    </>
                  )}
                </ul>
              </div>
            </div>
          </nav>
        </div>
      </div>
    </div>
  );
}
