import React,{useState} from "react";
import { useFormik } from "formik";
import { useSelector, useDispatch } from "react-redux";
import { SignUpRequest } from "../../Redux/Auth/actions";
import * as Yup from "yup";

//scss
import styles from "./style.module.scss";

const Form = ({ type }) => {
  const dispatch = useDispatch();
  const [btnState, setBtnState] = useState(false);
  const loginUserSchema = Yup.object().shape({
    fullName: Yup.string().trim()
      .max(16, "Fullname should not exceed 16 characters")
      .required("Please enter full name"),
    userName: Yup.string().trim()
      .max(16, "Username should not exceed 16 characters")
      .required("Please enter username"),
    email: Yup.string().trim()
      .email("Please enter valid email address")
      .required("Please enter email address"),
    password: Yup.string().trim()
      .required("Please enter password")
      .matches(
        /^(?=(?:.*[A-Z].*){1})(?=.*\d)(?=.*[@$!%*#?&])[A-Za-z\d@$!%*#?&]{8,}$/,
        "Must Contain 8 Characters,  One Uppercase, One Lowercase, One Number and one special case Character"
      ),
  });
  const formik = useFormik({
    initialValues: {
      email: "",
      password: "",
      fullName: "",
      userName: "",
    },
    validationSchema: loginUserSchema,
    onSubmit: (values) => {
      setBtnState(true);
      dispatch(SignUpRequest({
        email:values.email,
        password:values.password,
        fullName:values.fullName,
        userName:values.userName,
        setBtnState:setBtnState
      }));
    },
  });

  return (
    <div className="Container">
      <div className="row">
        <div className="col-12">
          <form onSubmit={formik.handleSubmit} className={styles.Form}>
            <label className="text-start">Email</label>
            <input
              fullWidth
              className={styles.input}
              name="email"
              placeholder="Enter Your Email"
              value={formik.values.email}
              onChange={formik.handleChange}
              helperText={formik.touched.email && formik.errors.email}
            />
            {formik.touched.email && formik.errors.email ? (
              <p className={styles.error}>{formik.errors.email}</p>
            ) : (
              ""
            )}
            <label className="text-start">Full name</label>
            <input
              className={styles.input}
              name="fullName"
              placeholder="Enter Full name"
              value={formik.values.fullName}
              onChange={formik.handleChange}
              helperText={formik.touched.fullName && formik.errors.fullName}
            />
            {formik.touched.fullName && formik.errors.fullName ? (
              <p className={styles.error}>{formik.errors.fullName}</p>
            ) : (
              ""
            )}
            <label className="text-start">Username</label>
            <input
              className={styles.input}
              name="userName"
              placeholder="Enter Username"
              value={formik.values.userName}
              onChange={formik.handleChange}
              helperText={formik.touched.userName && formik.errors.userName}
            />
            {formik.touched.userName && formik.errors.userName ? (
              <p className={styles.error}>{formik.errors.userName}</p>
            ) : (
              ""
            )}
            <label className="text-start">Password</label>
            <input
              name="password"
              type="password"
              placeholder="Enter Password"
              className={styles.input}
              value={formik.values.password}
              onChange={formik.handleChange}
              helperText={formik.touched.password && formik.errors.password}
            />
            {formik.touched.password && formik.errors.password ? (
              <p className={styles.error}>{formik.errors.password}</p>
            ) : (
              ""
            )}
            <button type="submit" disabled={btnState}>Create Account</button>
          </form>
        </div>
      </div>
    </div>
  );
};

export default Form;
